/* playfair-display-regular - cyrillic_latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: "Playfair Display";
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/playfair-display-v30-cyrillic_latin-regular.eot"); /* IE9 Compat Modes */
    src: url("../fonts/playfair-display-v30-cyrillic_latin-regular.eot?#iefix")
            format("embedded-opentype"),
        /* IE6-IE8 */
            url("../fonts/playfair-display-v30-cyrillic_latin-regular.woff2")
            format("woff2"),
        /* Super Modern Browsers */
            url("../fonts/playfair-display-v30-cyrillic_latin-regular.woff")
            format("woff"),
        /* Modern Browsers */
            url("../fonts/playfair-display-v30-cyrillic_latin-regular.ttf")
            format("truetype"),
        /* Safari, Android, iOS */
            url("../fonts/playfair-display-v30-cyrillic_latin-regular.svg#PlayfairDisplay")
            format("svg"); /* Legacy iOS */
}
